.login{
    width: 100vw;
    height: 100vh;
    background-color: #f0f2f5;
    display: flex;
    align-items: center;
    justify-content: center;
}
.loginWrapper {
    width: 70%;
    height: 70%;
    display: flex;
}
.loginLeft, .loginRight {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.loginLogo{
    font-weight: 800;
    font-size: 50px;
    color: #1775ee;
    margin-bottom: 10px;
}
.loginDesc {
    font-size: 24px;
}
.loginBox {
    height: 400px;
    padding: 20px;
    background-color: white;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.loginInput {
    height: 50px;
    border-radius: 10px;
    border: 1px solid gray;
    font-size: 18px;
    padding-left: 20px;
}
.loginInput:focus{
    outline: none;
}
.loginButton {
  height: 50px;
  border: none;
  border-radius: 10px;
  background-color: #1775ee;
  font-size: 20px;
  font-weight: 500;
  cursor: pointer;
  color: white;
}
.loginButton:disabled{
    cursor: not-allowed;
}
.loginForgot {
    text-align: center;
    color: #1775ee;
}
.loginRegisterButton {
    width: 60%;
    height: 50px;
    align-self: center;
    border: none;
    border-radius: 10px;
    background-color: #42b72a;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
    color: white; 
}